import React, { useCallback } from "react";

function App() {
  const handleUpload = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {},
    []
  );

  return (
    <input
      onChange={handleUpload}
      type="file"
      name="image"
      accept=".png, .jpg, .jpeg"
      capture="environment"
    />
  );
}

export default App;
